import stampit from 'stampit'

// setting some default values for the app
const defaults = stampit().refs({
  id : 312313,
  name : 'Gurminder',
  type : ['soloist'],
  homeTown : "Jalandhar",
  designation : "songWriter",
  company : "DoItLoud",
  songs : [{
    name : "NameOfSong",
    ref : "httpSpmething",
    songAddedOn : 11312321313
  }],
  created : (new Date()).getTime()
});
/*
	methods on artist
 */
// methods attached on main artist object
const artistMethods = stampit({
  methods: {
    setName(newName) {
      this.name = newName;
      return this;
    },
    getArtistName(name) {
      return this.name;
    },
    getSongs() {
      return this.songs;
    },
    getType() {
      return this.type;
    },
    getRole(){
      return "Artist";
    }
  }
});
// availibility of an artist
const availability = stampit().init(function() {
  var bookingDates = []; // private

  this.book = function book(timeStamp) {
    bookingDates.push(timeStamp);
    return this;
  };
  this.deleteBooking = function deleteBooking(timeStamp) {
    if(~bookingDates.indexOf(timeStamp))
      bookingDates.splice(bookingDates.indexOf(timeStamp),1);
    return this;
  };
  this.isOpenForBooking = function isOpenForBooking() {
    return (bookingDates.length ? bookingDates : true);
  }
});


const artistObject = stampit.compose(defaults, artistMethods,availability);
const artistObjectInstance = artistObject({name : 'Gury Gang Band',type: 'band'});
console.log(artistObjectInstance,artistObjectInstance.isOpenForBooking())
export default artistObject;


