import stampit from 'stampit'

// setting some default values for the app
const defaults = stampit().refs({
  id : 1231123,
  name : 'Hard Rock Cafe',
  city : "Gurgaon",
  type : ['cafe'],
  created : (new Date()).getTime()
});
/*
	methods on venue
 */
// methods attached on main venue object
const venueMethods = stampit({
  methods: {
    setName(newName) {
      this.name = newName;
      return this;
    },
    getVenueName(name) {
      return this.name;
    },
    getRole(){
      return "Venue";
    }
  }
});

// check the availability of venue
const availability = stampit().init(function() {
  var isOpen = false,
      bookingDates = []; // private


  this.open = function open() {
    isOpen = true;
    return this;
  };
  this.close = function close() {
    isOpen = false;
    return this;
  };
  this.isOpen = function isOpenMethod() {
    return isOpen;
  }
});


const venueObject = stampit.compose(defaults, venueMethods);
const venueObjectInstance = venueObject();
console.log(venueObjectInstance,venueObjectInstance.getRole())
export default venueObject;


