import React from 'react'
import classes from './CardsCaraousal.scss'
import Slider from 'react-slick'
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import FlatButton from 'material-ui/FlatButton';

const settings = {
  dots: true,
  infinite: true,
  speed: 500,
  slidesToShow: 3,
  slidesToScroll: 1,
  autoplay : true,
  autoplaySpeed : 1500
};
const styles = {
  card: {
    margin: '10px',
    cursor: 'pointer',
    maxWidth: '350px',
    display: 'inline-block'
  },
  cardMediaContainer : {
    overflow : "hidden",
    minHeight: '200px',
    maxHeight: '200px'
  }
};
function redirectToStudioPage(e){
  console.log(e)  
}
const CardsCaraousal = (props) => (
  <div className={classes.Grid}>
    {props.featuredStudiosList.map((tile,index) => (
      <div key={index} >
        <div key={index} className={classes['Grid-cell']} key={index}>
          <Card key={index}
            onTouchTap={redirectToStudioPage}
            style={styles.card}
            className={classes.cardContainer}
            >
            <CardMedia style={styles.cardMediaContainer}>
              <img src={tile.img} className={classes.image}/>
            </CardMedia>
            <CardTitle title={tile.title}/>
          </Card>
        </div>
      </div>
    ))}
  </div>
);


CardsCaraousal.propTypes = {
  featuredStudiosList : React.PropTypes.array.isRequired
}

export default CardsCaraousal



