import React from 'react';
import DropDownMenu from 'material-ui/DropDownMenu';
import MenuItem from 'material-ui/MenuItem';
import appObjectInstance from 'store/objectDefinations/app.js';

const styles = {
  customWidth: {
    width: 100,
  },
};

export default class CategorySelector extends React.Component {

  constructor(props) {
    super(props);
    this.state = {value: 1};
  }

  handleChange = (event, index, value) => this.setState({value});

  render() {
    return (
      <DropDownMenu
        value={this.state.value}
        onChange={this.handleChange}
        style={styles.customWidth}
        autoWidth={false}
      >
        <MenuItem value={1} primaryText={appObjectInstance.homePageConfig.Venue} />
        <MenuItem value={2} primaryText={appObjectInstance.homePageConfig.Artists} />
      </DropDownMenu>
    );
  }
}


