import stampit from 'stampit'

// setting some default values for the app
const defaults = stampit().refs({
  name : 'Gurminder',
  homeTown : "Jalandhar",
  created : (new Date()).getTime()
});
/*
	methods on user
 */
// methods attached on main user object
const userMethods = stampit({
  methods: {
    setName(newName) {
      this.name = newName;
      return this;
    },
    getUserName(name) {
      return this.name;
    },
    getsongs() {
      return this.songs;
    },
    getRole(){
      return "User";
    }
  }
});



const userObject = stampit.compose(defaults, userMethods);
const userObjectInstance = userObject({name : 'Gurminder'});
console.log(userObjectInstance,userObjectInstance.getRole())
export default userObject;


