import React from 'react'
import classes from './HomeView.scss'
import StudioTile from 'components/StudioTile'
import {Card, CardMedia} from 'material-ui/Card'
import Buttons from 'components/Buttons'
import Counter from 'components/Counter'
import IconMenu from 'material-ui/IconMenu'
import MenuItem from 'material-ui/MenuItem'
import IconButton from 'material-ui/IconButton/IconButton'
import RaisedButton from 'material-ui/RaisedButton'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import getMuiTheme from 'material-ui/styles/getMuiTheme'
import darkBaseTheme from 'material-ui/styles/baseThemes/darkBaseTheme'
import CardsCaraousal from 'components/CardsCaraousal'
import appObjectInstance from 'store/objectDefinations/app.js'
import artistObject from 'store/objectDefinations/artist/artist.js'
import userObject from 'store/objectDefinations/user/user.js'
import venueObject from 'store/objectDefinations/venue/venue.js'
import CategorySelector from 'components/CategorySelector'

console.log(appObjectInstance)

const {LocateButton} = Buttons;

	
const styles = {
    overlayConatainer : {
        top : 0,
        background : 'none',
        marginTop : '30%',
        fontSize : '1.5em',
        color : "white"
    },
    searchButton : {  
        margin : "0px 0 0 20px"
    },
    location : {
    }
}

export class HomeView extends React.Component {
    constructor(){
        super();
        this.getFeaturedStudios = this.getFeaturedStudios.bind(this);

    }
    //fires only on first render of component
    componentWillMount(){
        this.getFeaturedStudios();
    }
    //imp for memry leaks
    componentWillUnmount(){

    }
    getFeaturedStudios () {
        this.props.fetchFeaturedStudiosList();
    }
    render() {

        return (
		  <div className={classes.homeContainer}>
		      <Card>
                <CardMedia
                  overlay={
                    <MuiThemeProvider muiTheme={getMuiTheme(darkBaseTheme)}>
                        <div>
                            <div className={classes.mainHeading}>{appObjectInstance.homePageConfig.searchTitle}</div>
                            <div className={classes.toolsContainer}>
                                <CategorySelector />
                                <LocateButton LocateButtonText={appObjectInstance.homePageConfig.locateText}></LocateButton>
                                <RaisedButton label="Search" style={styles.searchButton} />
                            </div>
                        </div>
                    </MuiThemeProvider>
                  }
                  overlayContentStyle={styles.overlayConatainer}
                >
                    <img src="Live-music-bg.jpg" />
                </CardMedia>
              </Card>
              <div className={classes.featuredContainer}>
                <h1>{appObjectInstance.homePageConfig.featuredVenues}</h1>
                <CardsCaraousal featuredStudiosList={this.props.featuredStudiosList}></CardsCaraousal>
              </div>
		  </div>
		);
    }
}
//above card media
//<img src="background1.jpg" />
HomeView.propTypes = {
  featuredStudiosList: React.PropTypes.array.isRequired,
  increment : React.PropTypes.func.isRequired,
  fetchFeaturedStudiosList : React.PropTypes.func.isRequired

}
export default HomeView



